.table {
    max-width: 100%;
    width: auto;
    min-width: 100px;
    margin: 8px 0;

    thead, tbody, tfoot { // TODO: remove chaining when bootstrap dependency is removed
        > tr > th,
        > tr > td {
            border: 1px solid color('gray');
            padding: 6px;
            font-size: rem(1.5);
            min-height: 32px;
            height: 32px;
            min-width: 32px;
            vertical-align: top;
        }
    }

    td.inactive, th {
        background-color: color('gray-lightest');
    }

    md-input-container {
        margin: 0;
    }

    .md-errors-spacer {
        display: none;
    }
}

.table--student {
    td {
        &.inactive {
            padding: 8px 10px;
        }
    }
}

.table--full-width {
    width: 100%;
}

.table--list {
    border: 0 none;
    background-color: #ffffff;
    max-width: 100%;

    th, td {
        padding: 0 4px;
        border: 0 none;
    }

    td {
        min-height: 56px;
        height: 56px;
    }

    tr {
        &.md-button {
            display: table-row;
            text-align: left;
            width: auto;
            text-transform: none;
            font-size: inherit;
            font-weight: normal;
        }
    }
}

.table-wrap-sticky {
    @media only screen and (max-width: $layout-breakpoint-sm - 1) {
        overflow-x: auto;
    }
}

.table--list__thead {
    font-size: rem(1.4);
    font-weight: 700;
}

.table--list__thead__tr {
    height: 100%;
    margin: 0;
}

.table--list__thead__th {
    background-color: color('gray-darker');
    color: color('text-light');
    min-height: $wise-toolbar-height;
    height: $wise-toolbar-height;
}

.table--list__thead__link {
    color: #ffffff;
    text-transform: none;
    margin: 0;
    min-width: 0;
    white-space: normal;
    line-height: 1.4;
    width: 100%;
}

.table--list__thead__sort {
    margin: 0;
}

.table--list__thead__sort--reverse {
    transform: rotate(180deg);
}

.td--wrap {
    min-width: 180px;
    white-space: normal;
    line-height: 1.2;
}

.td--max-width {
    @media only screen and (max-width: $layout-breakpoint-sm - 1) {
        max-width: 180px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
